.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url('./assets/bg_side.svg');
  background-size: 100%;
  background-repeat: repeat-y;
  width: 100%;
  margin: 0;
  padding: 0;
}

.section-title {
  color: #082E00;
  font-size: 2.5rem;
  text-align: center;
  font-weight: bold;
  text-shadow: 0px 3px 3px rgba(0, 0, 0, 0.507);
}

.divider {
  display: flex;
  background-image: url('./assets/divider.png');
  width: 100vw;
  height: 15vw;
  max-height: 300px;
  min-height: 60px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  font-weight: bolder;
  color: white;
  margin-top: 20px;
  text-shadow: 0 0px 10px black;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f16c;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #00000065;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}